<template>
    <div class="content">
        <el-row type="flex" class="pedidos">
            <el-col v-if="!pedidos.length">
                <h1>Nenhum pedido planejado</h1>
            </el-col>
            <el-col class="cards" v-for="pedido in pedidosFormatted">
                <el-card class="cards">
                    <p>Nome: {{pedido.filho.nome}}</p>
                    <p>Turma: {{pedido.filho.turma}}</p>
                    <p>Período: {{pedido.filho.periodo}}</p>
                    <p class="xl-black">Data de entrega: {{pedido.delivery_date}}</p>
                    <p>Preço final: {{pedido.final_price}}</p>
                    <p>Foi entregue? <span :class="{green: pedido.entregue, red: !pedido.entregue}">{{pedido.entregue | entregar}}</span> <el-button type="text" v-if="!pedido.entregue" @click="deletarPed(pedido.id)">Cancelar pedido</el-button></p>
                    <p>Pedido:</p>
                    <el-table :data="pedido.list" style="width: 250px">
                        <el-table-column prop="produto" label="Prod." min-width="50">
                            <template scope="scope">
                                <div class="tableContent">
                                    {{scope.row.produto}}
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="qtde" label="Qtde." min-width="50" align="right"></el-table-column>
                        <el-table-column prop="unit" label="P.U." min-width="50" align="right"></el-table-column>
                    </el-table>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                pedidos: [],
                pedidosFormatted: []
            }
        },
        filters: {
            entregar(val) {
                return ((val) ? 'Sim' : 'Não' );
            }  
        },
        watch: {
            pedidos(ped) {
                if (!ped.length) {
                    this.pedidosFormatted = [];
                } else {
                    let filhos = JSON.parse(window.sessionStorage.getItem('filhos'));
                    ped.forEach((el, index) => {
                        const keys = Object.keys(filhos);
                        this.pedidosFormatted.push({});
                        this.pedidosFormatted[index].filho = filhos[keys.find(key => {
                            return filhos[key].id === el.aluno_id;
                        })];
                        let tur =  Object.assign({}, this.pedidosFormatted[index].filho);
                        let periodo, turma;
                        let conj = tur.turma.split(" ");
                        let per = this.$store.getters.getPeriodos;
                        periodo = per.find((cv) => {
                            return (cv.value === conj[1]);
                        });
                        let turm = this.$store.getters.getTurmas;
                        turma = turm.find((cv) => {
                            return (cv.value === conj[0]);
                        });

                        if ((typeof(turma) != "undefined") && (typeof(periodo) != "undefined")) {
                            this.pedidosFormatted[index].filho.turma = turma.label;
                            this.pedidosFormatted[index].filho.periodo = periodo.label;
                        } 
                        this.pedidosFormatted[index].id = el.id;
                        this.pedidosFormatted[index].delivery_date = el.delivery_date.substr(8, 2) + '/' + el.delivery_date.substr(5,2) + '/' + el.delivery_date.substr(0, 4);
                        this.pedidosFormatted[index].final_price = 'R$ ' + el.final_price.toFixed(2);
                        this.pedidosFormatted[index].final_price = this.pedidosFormatted[index].final_price.replace('.', ',');
                        this.pedidosFormatted[index].list = JSON.parse(el.list);
                        this.pedidosFormatted[index].list = this.pedidosFormatted[index].list.list; 
                        this.pedidosFormatted[index].entregue = el.type;
                    });
                
                }
            }
        },
        methods: {
            verPedidos() {
                axios.post('api/responsavel/pedidos/'+window.sessionStorage.getItem('user')+'/get')
                    .then(response => {
                        console.log('response');
                        console.log(response);
                        this.pedidos = response.data.pedidos.map(obj => {
                            return obj;
                        });
                        console.log(this.pedidos);
                    })
                    .catch(error => {
                        console.log('error');
                        console.log(error);
                    });
            },
            deletarPed(idPed) {
                this.$confirm('Deseja cancelar o pedido?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    axios.post('api/responsavel/pedido/'+ idPed +'/delete')
                        .then(response => {
                            console.log('response apagar');
                            console.log(response);
                            this.$message({
                                type: 'success',
                                message: response.data.sucesso
                            });
                            this.verPedidos();
                        })
                        .catch(error => {
                            console.log('error apagar');
                            console.log(error);
                            this.$message({
                                type: 'error',
                                message: 'Não foi possível cancelar!'
                            });
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: 'Pedido não foi cancelado!'
                    });
                });
            }
        },
        mounted() {
            this.verPedidos();
        }
    }
</script>
<style scoped>
    .content {
        margin-top: 3vh;
    }

    .pedidos {
        display: flex;
        flex-wrap: wrap;
        height: 97vh;
        overflow-y: visible;
        overflow-x: hidden;
    }

    .pedidos .cards {
        margin: 0 10px 10px 10px;
    }
    .cards div p {
        display: block;
    }

    .cards {
        width: 300px;
    }



    @media (max-width: 768px) {
        .pedidos {
            flex-direction: column;
        }
        .pedidos .cards {
            margin: 10px auto;
        }
    }
</style>


