<template>
    <div class="home dark-white-bg">
        <el-row :class="{home: true, 'dark-white-bg': true}">
            <el-col :span="4" :class="{menu: true, 'light-gray-bg': true}">
                <component :is="menu" :nivel="nivel" @opcao="principal"></component>
            </el-col>
            <el-col :span="20" :class="{content: true}">
                <el-button v-if="!isHome" @click="backToIndex" class="homeButton"><i class="material-icons">home</i></el-button>
                <component class="conteudo" :is="opcao" @voltar="backToIndex"></component>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import Menu from './Menu';
    import Pedidos from './Pedidos';
    import Lanches from './Lanches';
    import Refeicao from './Refeicao';
    import Responsavel from './Responsavel';
    import Estoque from './Estoque'; 
    import Opcoes from './Opcoes';
    import Cantina from './Cantina';

    export default {
        data() {
            return {
                menu: Menu,
                nivel: window.sessionStorage.getItem('nivel'),
                opcao: '',
                isHome: true,
                botao: ''
            }
        },
        watch: {
            opcao() {
                if (this.opcao === Responsavel || this.opcao === Cantina)
                    this.isHome = true;
                else
                    this.isHome = false;
            },
            botao(val) {
                console.log(val);
                let element = document.getElementById(val);
                let allElements = document.getElementsByClassName('light-black-bg');
                //console.log(allElements);
                [].forEach.call(allElements, function(el) {
                    el.classList.remove('light-black-bg');
                });
                
                element.classList.add('light-black-bg');
            }
        },
        methods: {
            principal(op) {
                if(this.verifyStates() && this.nivel === '2' ) {
                    this.$confirm('Deseja sair sem concluir?', 'Aviso', {
                        confirmButtonText: 'Sim',
                        cancelButtonText: 'Não',
                        type: 'warning'
                    }).then(() => {
                        this.$store.dispatch('deleteListaPedidos', []);
                        this.$store.dispatch('deleteListaRefeicao', []);
                        this.changeMenu(op[0]);
                    }).catch(() => {
                        (op[0] == 'lanches') ? this.botao = 'refeicao' : this.botao = 'lanches';
                    });
                } else {
                    this.changeMenu(op[0]);
                }
            },
            changeMenu(op) {
                switch(op) {
                    case 'pedidos':
                        this.opcao = Pedidos;
                        break;
                    case 'estoque':
                        this.opcao = Estoque;
                        break;
                    case 'opcoes':
                        this.opcao = Opcoes;
                        break;
                    case 'lanches':
                        this.opcao = Lanches;
                        break;
                    case 'refeicao':
                        this.opcao = Refeicao;
                        break;
                }
            },
            backToIndex() {
                let allElements = document.getElementsByClassName('light-black-bg');
                [].forEach.call(allElements, function(el) {
                    el.classList.remove('light-black-bg');
                });
                if(this.nivel == 2) {
                    this.opcao = Responsavel;
                } else if(this.nivel == 3) {
                    this.opcao = Cantina;
                }
            },
            verifyStates() {
                if(this.$store.getters.getListaPedidos.length || this.$store.getters.getListaRefeicao.length)
                    return true;
                return false;
            }

        },
        mounted() {
            this.backToIndex();
        },
        components: {
            Menu,
            Pedidos,
            Lanches,
            Refeicao,
            Responsavel,
            Estoque,
            Opcoes,
            Cantina
        }
    }
</script>
<style scoped>

    .homeButton {
        position: fixed;
        margin-top: 5px;
        margin-left: 5px;
        z-index: 5;
    }
    .home {
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        overflow-y: hidden;
    }
    el-row {
        background-color: orange;
    }
    .menu {
        height: 100vh;
        display: fixed;
    }

    #t {
        background-color: purple;
    }
    
    .content {
        font-family: 'Roboto', sans-serif;
    }

    @media (max-width: 768px) {
        .menu {
            display: flex;
            width: 100vw;
            height: 15vh;
            justify-content: center;
            order: 2;
            z-index: 10;
        }
        .content {
            width: 100vw;
            height: 85vh;
        }
        .home > * {
            display: flex;
            flex-direction: column;
        }
        .conteudo {
            margin-top: 5vh;
        }
    }
</style>


