<template>
    <div class="content">
        <el-row :class="{'row-bg': true}" type="flex">
            <!--el-col>
                <el-select v-model="filter" placeholder="Filtrar">
                    <el-option v-for="f in filters" :label="f.label" :value="f.value"></el-option>
                </el-select>
            </el-col-->
            <el-col class="trocar">
                Mude entre:<br/>
                Lanches <el-switch v-model="opcao" on-text="" off-text="" :class="{'light-silver': opcao, 'silver': !opcao}" ></el-switch> Refeições
            </el-col>
            <el-col>
                <el-table :data="tableData" class="tabela" max-height="400" border :default-sort = "{prop: 'delivery_date', order: 'descending'}">
                    <el-table-column type="expand">
                        <template scope="props">
                            <div v-for="el in props.row.pedidos.list" class="prods">
                                <span><b>Produto:</b> {{el.produto}}</span>
                                <span><b>QTDE:</b> {{el.qtde}}</span>
                            </div>
                            <div class="price">
                                <div v-for="el in props.row.pedidos.list"><span v-if="el.info" class="blue"><b>OBS:</b> {{el.info}}</span> </div>
                                <p class="green"><b>Preço Final:</b> R$ {{props.row.preco_final}}</p>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="nome" label="Nome" min-width="100" sortable>
                        <template scope="props">
                            <div class="tableContent">
                                {{props.row.nome}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="turma" label="Turma" min-width="100" sortable>
                        <template scope="props">
                            <div class="tableContent">
                                {{props.row.turma}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="periodo" label="Período" min-width="110" :filter-method="filterPeriodo" :filters="[{text: 'MANHÃ', value: 'MANHÃ'}, {text: 'TARDE', value: 'TARDE'}, {text: 'INTEGRAL', value: 'INTEGRAL'}]">
                        <template scope="props">
                            <div class="tableContent">
                                {{props.row.periodo}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="cat" label="Categoria" min-width="100">
                        <template scope="scope">
                            <el-tag close-transition :type="tipoTag(scope.row.cat)">{{scope.row.cat}}</el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="delivery_date" label="Data" min-width="120" sortable>
                        <template scope="scope">
                            <span v-if="scope.row.delivery_date">{{scope.row.delivery_date.slice(8,10)}}/{{scope.row.delivery_date.slice(5, 7)}}/{{scope.row.delivery_date.slice(0,4)}}</span>
                        </template>
                    </el-table-column>
                    <!--el-table-column prop=-->
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                filters: [{
                    label: 'Ano',
                    value: 'ano'
                }, {
                    label: 'Nome',
                    value: 'nome'
                }, {
                    label: 'Horário',
                    value: 'horario'
                }],
                filter: '',
                tableData: [],
                opcao: false,
                pedidosLanches: [],
                pedidosRefeicao: [],
                info: false
            }
        },
        watch: {
            opcao() {
                this.request();
            },
            pedidosRefeicao() {
                let table = this.pedidosRefeicao;
                this.tableData = [];
                this.pedidos(table, 'Refeição');
            },
            pedidosLanches() {
                let table = this.pedidosLanches;
                this.tableData = [];
                this.pedidos(table, 'Lanche');
            }
        },
        mounted() {
            this.request();
        },
        methods: {
            filterPeriodo(value, row) {
                return row.periodo === value;
            },
            tipoTag(type) {
                if (type === 'Refeição')
                    return 'primary';
                else if (type === 'Lanche')
                    return 'success';
            },
            pedidos(table, tipo) {
                for (let n in table) {
                    //console.log(table[n]);
                    table[n].cat = tipo;
                    let ped = table[n].pedidos;
                    //console.log(typeof ped+ ' ' + ped);
                    let pedJSON = JSON.parse(ped);
                    //console.log(typeof ped, '1');
                    //console.log(pedJSON);
                    table[n].pedidos = pedJSON;
                    //console.log(table[n].preco_final.toString());
                    const turmas = this.$store.getters.getTurmas;
                    const periodos = this.$store.getters.getPeriodos;
                    const sep = table[n].turma.split(" ");
                    let turma = turmas.find((cv) => {
                        return (cv.value === sep[0]);
                    });
                    let periodo = periodos.find((cv) => {
                        return (cv.value === sep[1]);
                    });
                    //console.log(turma);
                    //console.log(periodo);

                   if ((typeof(turma) != "undefined") && (typeof(periodo) != "undefined")) {
                        table[n].turma = turma.label;
                        table[n].periodo = periodo.label;
                    }

                    table[n].preco_final = table[n].preco_final.toFixed(2);
                    this.tableData.unshift(table[n]);

                }
            },
            request() {
                const lanches = 'api/cantina/pedidos/get';
                const refeicao = 'api/cantina/pedidos/refeicoes';
                let op = '';
                (!this.opcao) ? op = lanches : op = refeicao;
                axios.post(op, {nivel: window.sessionStorage.getItem('nivel')})
                    .then(response => {
                        //console.log('response');
                        //console.log(response);
                        (this.opcao) ? this.pedidosRefeicao = response.data : this.pedidosLanches = response.data;
                        
                    }).catch(error => {
                        console.log('error');
                        console.log(error);
                    });
            }
        }
    }
</script>
<style scoped>

.content {
        height: 100vh;
        display: flex;
        justify-content: center;
    }

    .row-bg {
        flex-direction: column;
        margin-top: 3vh;
        align-items: center;
    }

    .tabela {
        width: 100%;
    }

    .prods {
        display: flex;
        justify-content: space-between;
    }
    .price {
        margin-top: 5px;
    }

    .trocar {
        text-align: center;
        margin-bottom: 5px;
    }
</style>


