import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const state = {
    dateLanche: '',
    dateRefeicao: '',
    filhos: '', // JSON.parse(window.sessionStorage.getItem('filhos')),
    listaPedidos: [],
    listaRefeicao: [],
    filhosPedidos: '',
    filhosRefeicao: '',
    listaPedidosFormatted: [],
    periodos: [{
        label: 'MANHÃ',
        value: 'manha'
    }, {
        label: 'TARDE',
        value: 'tarde'
    }, {
        label: 'INTEGRAL',
        value: 'integral'
    }],
    turmas: function() {
        let t = [];
        t.push({ label: 'INFANTIL 1', value: 'inf1' });
        t.push({ label: 'INFANTIL 2', value: 'inf2' });
        for (let i = 1; i < 10; i++) {
            t.push({ label: i + ' ANO', value: i + 'ano' });
        }
        return t;
    }()
};

const getters = {
    getListaPedidos(state) {
        return state.listaPedidos;
    },
    getListaRefeicao(state) {
        return state.listaRefeicao;
    },
    getFilhosPedidos(state) {
        return state.filhosPedidos;
    },
    getFilhosRefeicao(state) {
        return state.filhosRefeicao;
    },
    getListaPedidosFormatted(state) {
        return state.listaPedidosFormatted;
    },
    getFilhos(state) {
        return state.filhos;
    },
    getDateLanche(state) {
        return state.dateLanche;
    },
    getDateRefeicao(state) {
        return state.dateRefeicao;
    },
    getPeriodos(state) {
        return state.periodos;
    },
    getTurmas(state) {
        return state.turmas;
    }
};

const actions = {
    addListaPedidos(context, item) {
        context.commit('addListaPedidos', item);
    },
    removeListaPedidos(context, item) {
        context.commit('removeListaPedidos', item);
    },
    deleteListaPedidos(context, lista) {
        context.commit('deleteListaPedidos', lista);
    },
    addListaRefeicao(context, item) {
        context.commit('addListaRefeicao', item);
    },
    removeListaRefeicao(context, item) {
        context.commit('removeListaRefeicao', item);
    },
    deleteListaRefeicao(context, lista) {
        context.commit('deleteListaRefeicao', lista);
    },
    changeFilhosPedidos(context, item) {
        context.commit('changeFilhosPedidos', item);
    },
    changeFilhosRefeicao(context, item) {
        context.commit('changeFilhosRefeicao', item);
    },
    listaPedidosFormatted(context, lista) {
        context.commit('listaPedidosFormatted', lista);
    },
    addFilhos(context, item) {
        context.commit('addFilhos', item);
    },
    addDateLanche(context, value) {
        context.commit('addDateLanche', value);
    },
    addDateRefeicao(context, value) {
        context.commit('addDateRefeicao', value);
    }
};

const mutations = {
    addListaPedidos(state, item) {
        state.listaPedidos.unshift(item);
    },
    removeListaPedidos(state, item) {
        const index = state.listaPedidos.indexOf(item);

        if (index != -1)
            state.listaPedidos.splice(index, 1);
    },
    deleteListaPedidos(state, lista) {
        state.listaPedidos = lista;
    },
    deleteListaRefeicao(state, lista) {
        state.listaRefeicao = lista;
    },
    addListaRefeicao(state, item) {
        state.listaRefeicao.unshift(item);
    },
    removeListaRefeicao(state, item) {
        const index = state.listaRefeicao.indexOf(item);
        if (index != -1)
            state.listaRefeicao.splice(index, 1);
    },
    changeFilhosPedidos(state, item) {
        state.filhosPedidos = item;
    },
    changeFilhosRefeicao(state, item) {
        state.filhosRefeicao = item;
    },
    listaPedidosFormatted(state, lista) {
        state.listaPedidosFormatted = lista;
    },
    addFilhos(state, lista) {
        state.filhos = lista;
    },
    addDateLanche(state, value) {
        state.dateLanche = value;
    },
    addDateRefeicao(state, value) {
        state.dateRefeicao = value;
    }
};



export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
});


