<template>
    <div class="content">
        <template v-if="nivel == 3">
            <div class="principal">
                <el-row>
                    <el-col>
                        <p>Gerar PDF da lista de pedidos</p>
                    </el-col>
                    <el-col>
                        <el-button type="primary" disabled>Gerar PDF</el-button>
                    </el-col>
                </el-row>
            </div>
        </template>
        <template v-else-if="nivel == 2">
            <div class="principal">
                <el-row>
                    <el-col>
                        <el-button type="text" @click="addFilho">Adicionar Filho</el-button>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :class="{tabelaC: true}">
                        <el-table height="450" border :data="tableData"  :class="{tabela: true}">
                            <el-table-column prop="name" label="Nome" align="left" min-width="75">
                                <template scope="scope">
                                    <div class="tableContent">
                                        {{scope.row.name}}
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="turma" label="Turma" align="left" min-width="75">
                                <template scope="scope">
                                    <div class="tableContent">
                                        {{scope.row.turma}}
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="periodo" label="Período" align="left" min-width="80">
                                <template scope="scope">
                                    <div class="tableContent">
                                        {{scope.row.periodo}}
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column label="Operações" width="105">
                                <template scope="scope">
                                    <div class="op">
                                        <el-button size="small" @click="editarFilho(scope.$index, scope.row)">Editar</el-button>
                                        <el-button size="small" type="danger" @click="deletarFilho(scope.$index, scope.row)">Apagar</el-button>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col> 
                </el-row>
            </div>


            <el-dialog :title="form.title" v-model="addFilhos">
                <el-input placeholder="Nome do filho" v-model="form.name"></el-input>
                <el-select v-model="form.turma" placeholder="Turma">
                    <el-option v-for="turma in turmas" :label="turma.label" :value="turma.value"></el-option>
                </el-select>
                <el-select v-model="form.periodo" placeholder="Período">
                    <el-option v-for="p in periodos" :label="p.label" :value="p.value"></el-option>
                </el-select>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addFilhos = false">Cancelar</el-button>
                    <el-button type="primary" @click="saveFilho" v-loading="loading" element-loading-text="Carregando">Salvar Filho</el-button>
                </span>

            </el-dialog>

        </template>
        
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                nivel: window.sessionStorage.getItem('nivel'),
                tableData: [],
                filhos: [],
                addFilhos: false,
                form: {
                    id: '',
                    name: '',
                    turma: '',
                    periodo: '',
                    edit: false,
                    title: 'Adicionar Filho'
                },
                periodos: [{
                    label: 'MANHÃ',
                    value: 'manha'
                }, {
                    label: 'TARDE',
                    value: 'tarde'
                }, {
                    label: 'INTEGRAL',
                    value: 'integral'
                }],
                loading: false
            }
        },
        watch: {
            filhos(filhos) {
                console.log(filhos);
                this.tableData = [];
                for(let filho in filhos) {
                    // o primeiro é a turma, o segundo é o período
                    let conj = filhos[filho].turma.split(" ");
                    let periodo = this.periodos.find((cv) => {
                        return (cv.value === conj[1]);
                    });
                    let turma = this.turmas.find((cv) => {
                        return (cv.value === conj[0]);
                    });
                    if(turma && periodo)
                        this.tableData.push({id: filhos[filho].id, name: filhos[filho].nome, turma: turma.label, periodo: periodo.label});
                }
            }
        },
        computed: {
            turmas() {
                let t = [];
                t.push({label: 'INFANTIL 1', value: 'inf1'});
                t.push({label: 'INFANTIL 2', value: 'inf2'});
                for (let i = 1; i < 10; i++) {
                    t.push({label: i + ' ANO', value: i + 'ano'});
                }
                return t;
            }
        },
        methods: {
            addFilho() {
                this.form.id = '';
                this.form.name = '';
                this.form.turma = '';
                this.form.periodo = '';
                this.form.title = 'Adicionar Filho';
                this.addFilhos = true;
            },
            saveFilho() {
                this.loading = true;
                let filho = Object.assign({}, this.form);
                let url = 'api/responsavel/filho/edit/'+filho.id.toString();
                filho.turma = filho.turma + ' ' + filho.periodo.toString();
                filho.name = filho.name.toUpperCase();
                if (!filho.edit) {
                    url = 'api/responsavel/filho/criar/'+window.sessionStorage.getItem('user');
                    delete filho.id;
                    const data = new Date();
                    filho.email = 'filho'+data.getTime()+'@email.com';
                }
                delete filho.periodo;
                delete filho.edit;
                delete filho.title;
                console.log(filho);
                axios.post(url, filho).then(response => {
                    console.log('response ' + url);
                    console.log(response);
                    this.loading = false;
                    if (response.data.sucesso) {
                        this.$message.success(response.data.sucesso);
                        
                        this.addFilhos = false;
                        this.request();
                    }
                }).catch(error => {
                    console.log('error');
                    console.log(error);
                    this.loading = false;
                    this.$message.error('Ocorreu um erro. Filho não foi salvo');
                });
            },
            request() {
                axios.post('api/responsavel/'+window.sessionStorage.getItem('user')+'/filhos')
                    .then(response => {
                        //console.log('response');
                        this.filhos = response.data.filhos;
                        window.sessionStorage.setItem('filhos', JSON.stringify(this.filhos));
                        this.$store.dispatch('addFilhos', JSON.parse(window.sessionStorage.getItem('filhos')));
                        //console.log(response);
                    })
                    .catch(error => {
                        console.log('error');
                        console.log(error);
                    });
            },
            editarFilho(index, row) {
                this.form.name = row.name;
               // this.form.turma = row.turma;
               // this.form.periodo = row.periodo;
                let periodo = this.periodos.find((cv) => {
                    return (cv.label === row.periodo);
                });
                let turma = this.turmas.find((cv) => {
                    return (cv.label === row.turma);
                });
                this.form.turma = turma.value;
                this.form.periodo = periodo.value;
                this.form.edit = true;
                this.form.id = row.id;

                this.form.title = "Editar Produto";
                this.addFilhos = true;
            },
            deletarFilho(index, row) {
                const idFilho = row.id;
                const idPai = window.sessionStorage.getItem('user');
                this.$confirm('Deseja mesmo apagar '+row.name+'?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    axios.post('api/responsavel/filho/delete/'+idFilho+'/'+idPai)
                    .then(response => {
                        console.log('response');
                        console.log(response);
                        this.$message({
                            type: 'success',
                            message: row.name+' apagado com sucesso!'
                        });
                        this.request();
                    })
                    .catch(error => {
                        console.log('error');
                        console.log(error);
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: row.name+' não foi apagado!'
                    });
                });
                
            }
        },
        mounted() {
            if (this.nivel == 2)
                this.request();
        }
    }
</script>
<style scoped>
    .content {
        height: 90vh;
        text-align: center;
        margin-top: 10vh;
    }

    .principal {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .tabela {
        width: 406px;
    }


    .tabelaC {
        display: flex;
        justify-content: center;
    }

    .op {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .op > * {
        margin-left: 0 !important;
        margin-top: 5px;
        margin-bottom: 5px;
    }
</style>


